import React, { Component, Fragment } from 'react';
import { CustomInput } from 'reactstrap';

export default class VersionsList extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    onVersionChange = function (e) {
        this.props.onRedirect(`/v/${e.target.value}`);
    }.bind(this)

    render() {
        return (
            <div >
                <h3>Version history</h3>
                <div className="version-list">
                    <a className={(this.props.selected === '1.2.2')?`active`:``} href="1.2.2">v. 1.2.2 (latest)</a>
                    {/*<a className={(this.props.selected === '1.2.1')?`active`:``} href="1.2.1">v. 1.2.1</a>
                    <a className={(this.props.selected === '1.2.0')?`active`:``} href="1.2.0">v. 1.2.0</a>*/}
                </div>
            </div>
        );

    }
}